"use client";

import { useState, useRef } from "react";
import Image from "next/image";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import type { Template } from "@/lib/definitions";
import { Eye, Copy } from "lucide-react";

interface TemplateCardProps {
  template: Template;
  onPreview: (template: Template) => void;
}

export function TemplateCard({ template, onPreview }: TemplateCardProps) {
  const [isHovered, setIsHovered] = useState(false);
  const videoRef = useRef<HTMLVideoElement>(null);

  const handleMouseEnter = () => {
    setIsHovered(true);
    if (videoRef.current) {
      // play() returns a promise, we can ignore the error if it's interrupted.
      videoRef.current.play().catch(() => {});
    }
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
    if (videoRef.current) {
      videoRef.current.pause();
    }
  };

  return (
    <Card
      className="overflow-hidden break-inside-avoid shadow-md transition-all duration-300 hover:shadow-xl hover:scale-105"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <CardContent className="p-0 relative aspect-[9/16]">
        <div className="absolute inset-0">
          {isHovered && template.vidUrl ? (
            <video
              ref={videoRef}
              src={template.vidUrl}
              className="h-full w-full object-cover"
              loop
              muted
              playsInline
            />
          ) : (
            <Image
              src={template.thumbUrl}
              alt={template.name}
              fill
              className="object-cover"
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            />
          )}
        </div>
        <div className="absolute top-2 right-2 flex flex-col items-end gap-2">
            {template.pro && <Badge variant="destructive">PRO</Badge>}
            {template.duplicateCount > 1 && (
              <Badge variant="default" className="flex items-center gap-1">
                <Copy className="h-3 w-3" />
                {template.duplicateCount}
              </Badge>
            )}
        </div>
      </CardContent>
      <CardFooter className="flex flex-col items-start gap-2 p-4">
        <h3 className="font-semibold truncate w-full">{template.name}</h3>
        <div className="flex flex-wrap gap-1">
          {template.category.map((cat) => (
            <Badge key={cat} variant="secondary">{cat}</Badge>
          ))}
        </div>
        <Button
          className="w-full mt-2"
          onClick={() => onPreview(template)}
          variant="outline"
        >
          <Eye className="mr-2 h-4 w-4" />
          Preview
        </Button>
      </CardFooter>
    </Card>
  );
}
