"use client";

import { useState } from "react";
import Link from "next/link";
import { usePathname, useSearchParams } from "next/navigation";
import { TemplateCard } from "./template-card";
import type { Template } from "@/lib/definitions";
import { TemplatePreviewModal } from "./template-preview-modal";
import { Button } from "@/components/ui/button";
import { ChevronLeft, ChevronRight } from "lucide-react";

interface TemplateGridProps {
  templates: Template[];
  categories: string[];
  totalPages: number;
  currentPage: number;
  activeFilter: string;
}

export function TemplateGrid({
  templates,
  categories,
  totalPages,
  currentPage,
  activeFilter,
}: TemplateGridProps) {
  const [selectedTemplate, setSelectedTemplate] = useState<Template | null>(
    null
  );
  const pathname = usePathname();
  const searchParams = useSearchParams();

  const createPageURL = (pageNumber: number | string) => {
    const params = new URLSearchParams(searchParams);
    params.set("page", pageNumber.toString());
    return `${pathname}?${params.toString()}`;
  };

  const createCategoryURL = (category: string) => {
    const params = new URLSearchParams(searchParams);
    params.set("category", category);
    params.set("page", "1"); // Reset to first page on category change
    return `${pathname}?${params.toString()}`;
  }

  const handlePreview = (template: Template) => {
    setSelectedTemplate(template);
  };

  const handleCloseModal = () => {
    setSelectedTemplate(null);
  };

  return (
    <>
      <div className="flex flex-wrap items-center gap-2 pb-4">
        <Button
          asChild
          variant={activeFilter === "All" ? "default" : "outline"}
        >
          <Link href={createCategoryURL("All")}>All</Link>
        </Button>
        {categories.map((category) => (
          <Button
            key={category}
            asChild
            variant={activeFilter === category ? "default" : "outline"}
          >
            <Link href={createCategoryURL(category)}>{category}</Link>
          </Button>
        ))}
      </div>

      <div className="columns-1 gap-4 sm:columns-2 md:columns-3 lg:columns-4 xl:columns-5 2xl:columns-6">
        {templates.map((template) => (
          <div key={template.uniqueKey} className="mb-4">
            <TemplateCard template={template} onPreview={handlePreview} />
          </div>
        ))}
      </div>
      {templates.length === 0 && (
        <div className="text-center text-muted-foreground py-16">
          No templates found for &quot;{activeFilter}&quot;.
        </div>
      )}

      {totalPages > 1 && (
         <div className="flex items-center justify-center gap-4 mt-8">
            <Button asChild variant="outline" disabled={currentPage <= 1}>
                <Link href={createPageURL(currentPage - 1)}>
                    <ChevronLeft /> Previous
                </Link>
            </Button>
            <span>Page {currentPage} of {totalPages}</span>
            <Button asChild variant="outline" disabled={currentPage >= totalPages}>
                <Link href={createPageURL(currentPage + 1)}>
                    Next <ChevronRight />
                </Link>
            </Button>
        </div>
      )}

      <TemplatePreviewModal
        template={selectedTemplate}
        isOpen={!!selectedTemplate}
        onClose={handleCloseModal}
      />
    </>
  );
}
