"use client";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";
import { Player } from "@lottiefiles/react-lottie-player";
import type { Template } from "@/lib/definitions";

interface TemplatePreviewModalProps {
  template: Template | null;
  isOpen: boolean;
  onClose: () => void;
}

export function TemplatePreviewModal({
  template,
  isOpen,
  onClose,
}: TemplatePreviewModalProps) {
  if (!template) return null;

  const lottieUrl = template.widgetsDefinition[0]?.URL;

  const aspectRatio = template.Width && template.Height ? template.Width / template.Height : 16 / 9;
  const width = 250;
  const height = width / aspectRatio;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-fit bg-background">
        <DialogHeader>
          <DialogTitle className="font-headline">{template.name}</DialogTitle>
          <DialogDescription>
            Live Skottie animation preview.
          </DialogDescription>
        </DialogHeader>
        <div 
          className="w-full overflow-hidden rounded-lg bg-muted flex items-center justify-center"
          style={{ width: `${width}px`, height: `${height}px` }}
        >
          {lottieUrl ? (
            <Player
              autoplay
              loop
              src={lottieUrl}
              style={{ height: '100%', width: '100%' }}
            />
          ) : (
            <p className="text-muted-foreground">No preview available.</p>
          )}
        </div>
      </DialogContent>
    </Dialog>
  );
}
